<template>
  <div class="t_bg">
    <!--  导航栏-->
    <NavBar class="t_nav">
      <i slot="left" class="el-icon-back" @click="back"></i>
      <div slot="center">初声</div>
    </NavBar>
    <BetterScroll class="content" ref="scroll" :probe-type="3" @scroll="contentScroll" :pull-up-load="true"
                  @pullingUp="loadMore">
      <!--    翻译卡片-->
      <el-card class="e_card">
        <!--      喇叭-->
        <div @click="play">
          <el-card class="e_trans" shadow="never">
            <div class="laba"><img src="@/assets/img/transform/laba.png"></div>
            <div class="num" v-if="isPlay">
              {{ progressStart }}
              <img src="@/assets/img/transform/dian.png">
              <img src="@/assets/img/transform/dian.png" style="margin-left: 5px">
            </div>
            <div v-else class="num">
              {{ audioPalyTime > 0 ? audioPalyTime : progressStart }}
              <img src="@/assets/img/transform/dian.png">
              <img src="@/assets/img/transform/dian.png" style="margin-left: 5px">
            </div>
          </el-card>
        </div>
        <!--      翻译正文-->
        <el-card class="e_body" shadow="never">
          <span class="span1">翻译:</span>
          <span class="span2">{{ transList.content }}</span>
        </el-card>
        <!--      建议正文-->
        <el-card class="e_body" shadow="never">
          <span class="span1">建议:</span>
          <span class="span2">{{ transList.cryMsg }}</span>
        </el-card>
        <!--      温馨提示正文-->
        <el-card class="e_body" shadow="never">
          <span class="span1">温馨提示:</span>
          <span class="span2">{{ transList.cryTip }}</span>
        </el-card>
      </el-card>
      <!--反馈部分-->
      <div @click="feedbackDialogVisible = true" class="feedback">
        我要反馈
      </div>
      <!--    相关推荐-->
      <div class="rec">
        <img src="@/assets/img/transform/recommend.png">
      </div>
      <!--    推荐内容-->
      <div>
        <GoodsList :goods="goods.records"/>
      </div>
    </BetterScroll>
    <!--    反馈-->
    <el-dialog
      title="反馈"
      :visible.sync="feedbackDialogVisible"
      width="95%"
      @close="feedbackDialogClosed"
    >
      <!--      内容主体-->
      <el-form :model="feedbackForm" :rules="feedbackFormRules" ref="feedbackFormRef" label-width="80px">
        <span>&nbsp;&nbsp;您对我们翻译的结果满意吗？或者您对我们有更多的建议可以写出。</span>
        <el-form-item label="满意:">
          <el-radio-group v-model="feedbackForm.isSatisfied">
            <el-radio :label="'满意'">满意</el-radio>
            <el-radio :label="'不满意'">不满意</el-radio>
          </el-radio-group>
        </el-form-item>
        <div>
          <el-form-item label="真实含义:">
            <el-select v-model="feedbackForm.cryTranslateWordsId" placeholder="请选择">
              <el-option
                v-for="item in rolesList"
                :key="item.id"
                :label="item.cryChinese"
                :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
        </div>
        <el-form-item label="建议:">
          <el-input
            type="textarea"
            :rows="2"
            placeholder="请输入内容"
            v-model="feedbackForm.message">
          </el-input>
        </el-form-item>
      </el-form>
      <!--          底部区-->
      <span slot="footer" class="dialog-footer">
        <el-button @click="this.feedbackDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addFeedback">确 定</el-button>
      </span>
    </el-dialog>
    <BackTop @click.native="backTop" v-show="isShow"/>
  </div>
</template>

<script>
import NavBar from "@/components/navbar/NavBar";
import GoodsList from "@/components/goods/GoodsList";
import BetterScroll from "@/components/betterScroll/BetterScroll";
import BackTop from "@/components/backTop/BackTop";


export default {
  name: "Transform",
  data() {
    return {
      transList: [],

      isShow: false,
      video: [],
      quithome:1,

      timer: '',
      audioTime: '',
      audioPalyTime: 0,
      progressStart: 0,
      isPlay: false,

      feedbackDialogVisible: false,//显示与隐藏反馈对话框
      feedbackForm: {
        isSatisfied:'满意',
        cryTranslateWordsId:0,
        message:'',
        fileUrl:''
      },//反馈的表单数据
      feedbackFormRules: {},//反馈表单的验证规则对象

      rolesList: [],//所有角色的数据列表

      goods: {},
    }
  },
  methods: {
    getRolesList(){
      this.$http.get('cryTranslateWords').then(res => {
        for (var i=0;i<res.data.data.records.length;i++){
          if (res.data.data.records[i].type === '1'){
            this.rolesList[i] = res.data.data.records[i]
          }
        }
      }).catch(err => err)
    },
    //获取文章
    getArticle() {
      this.$http.get('article').then(res => {
        this.goods = res.data.data
      }).catch(err => err)
    },
    back() {
      this.quithome = 2
      this.feedbackDialogVisible = true
    },
    play() {
      if (!this.video) {
        this.$message.warning('没有音频')
      } else {
        if (this.isPlay) {
          this.video.pausePlay()
          this.progressStart = 0
          this.isPlay = false
          clearTimeout(this.timer)
        } else {
          this.playRecorder()
          this.isPlay = true
          this.progress()
        }
      }
    },
    progress() {
      // 播放过程
      let timeStart = 1
      let audioTime = this.audioTime * 100
      this.count = timeStart
      // 记录时间
      this.timer = setInterval(() => {
        if (this.count <= audioTime) {
          this.count++
          this.progressStart = parseInt(this.count / 100)
        } else {
          this.isPlay = false
          this.progressStart = 0
          // 清楚定时器
          clearInterval(this.timer)
          this.timer = null
        }
      }, 10)
    },
    backTop() {
      this.$refs.scroll.scrollTo(0, 0)
    },
    contentScroll(position) {
      this.isShow = (-position.y) > 240
    },
    loadMore() {
      this.$refs.scroll.refresh()
    },
    // 录音播放
    playRecorder() {
      this.video.play()
    },
    //监听添加反馈对话框的关闭事件
    feedbackDialogClosed() {
      this.$refs.feedbackFormRef.resetFields()
    },
    //点击按钮添加反馈
    addFeedback() {
      this.$refs.feedbackFormRef.validate( valid => {
        if (!valid) return
        this.$http.post('feedback',this.feedbackForm).then(res => {
          if (this.quithome === 2){
            this.$router.push('/home')
          }
          this.$message.success('感谢您的建议与反馈')
          //隐藏添加的用户对话框
          this.feedbackDialogVisible = false
          //清空添加用户对话框
          this.$refs.feedbackFormRef.resetFields()
        }).catch(err => err)
      })
    }
  },
  components: {
    NavBar,
    GoodsList,
    BetterScroll,
    BackTop
  },
  mounted() {

    setTimeout(() => {
      this.video = this.$route.params.msgKey
      this.feedbackForm.fileUrl = this.$route.params.transform.path
      this.transList = this.$route.params.transform.cryWord
      // 获取录音的时间长度
      if (this.video) {
        this.audioPalyTime = Math.round(this.video.duration)
        this.audioTime = Math.round(this.video.duration)
      }
    }, 500)
    this.getArticle()
    this.getRolesList()
  }
}
</script>

